
import {createSlice} from '@reduxjs/toolkit'

let init={
    vlas:{
        
    },
    sideState:false
}
const Auth = createSlice({
    name:'Auth',
    initialState:init,
    reducers:{
        setVals(state,payload){
            state.vlas=payload
        },
        setside(state,payload){
            state.sideState=payload
        }
    }
})

export const Authaction = Auth.actions
export default Auth.reducer