import {useContext, useEffect, useState} from 'react'
import './App.css';
import ReactPlayer from 'react-player'

import Create from './components/Context/Create'
import Nav from './components/Header/Nav';
import Infi from './components/Infinity/Infi';
import Spot from './components/Spot/Spot';
function App() {

  const [latesturl,setLatestUrl] = useState({author: 'Urs Leutenegger', desc: 'On August 11, 2021 a multi-mirror, 17 meter-diamet… to compete with the bright light of a Full Moon.', title: 'Perseids and MAGIC', url: 'https://apod.nasa.gov/apod/image/2208/MagicPerseid001_1024.jpg'})
  const [sidevz,setSidev]  = useState(false)
  const [playurl,setPlayurl] = useState(latesturl.url)
    function chanurl(props){
        setPlayurl(props)
    }
    let val={
        latesturl:latesturl,
        setLatestUrl:setLatestUrl,
        sidevz:sidevz,
        setSidev:setSidev,
        setPlayurl:chanurl
       
    }

    useEffect(()=>{
      console.log(latesturl)
    },[latesturl])
    function cncl(){
     setSidev(false)
    }
  
  return (
    <Create.Provider value={val}>
{!sidevz?<div className='whole_app'>
    <Nav/>
    <Spot/>
    <Infi/>
   </div>
:<div className='whole_side_main'>
   <div className='whole_side'>
    <div className='cop'><div className='cancel' onClick={cncl}></div></div>
    <div className='cnt'>{console.log(playurl)}
   {playurl.includes('.jpg')?
   
  <img className='imgmag' src={playurl}></img>:
  <ReactPlayer pip={true}  height='60vh' width='40vw' url={playurl}></ReactPlayer>
  }
    </div>
   </div>
   </div>} 

    </Create.Provider>
   
  );
}

export default App;
