import './Spot.css'
import {useState,useContext, useEffect} from 'react'
import Create from '../Context/Create'

const Spot = ()=>{
   let auth = useContext(Create)

    const [latest,getLatest] = useState()
    var dat = new Date();
    function removeTime(date = new Date()) {

      return date.getFullYear()+'-'+date.getMonth()+'-'+date.getDate()
    }
    
    
  
    async function latestftc (){
        let resp =await fetch(`https://api.nasa.gov/planetary/apod?api_key=gaff4Pwpu0Qg6woyFty1YhVRxhj4In1ImvOCyFD7&date=${removeTime(dat)}`)
        let data = await resp.json()
       
       let team = {
        author:[data][0].copyright,
        desc:[data][0].explanation,
        title:[data][0].title,
        url:[data][0].url,
        media_type:[data][0].media_type
       }
       
     auth.setLatestUrl(team)
    }
    useEffect(()=>{
        latestftc()
    },[])
    function clki(){
      auth.setSidev(true)
    }
    return(
       <div className='st_whole'>

        <div className='st_txt'>
            <span className='st_title'>{auth.latesturl.title}</span>
          <p className='st_desc'>{auth.latesturl.desc}</p>
           
            <span className='st_auth'>-{auth.latesturl.author} </span>
        </div>
        <img className='st_img' src={auth.latesturl.url} onClick={clki}></img>
       </div>
    )
}

export default Spot