
import { useEffect, useState ,useContext} from 'react'
import './Infi.css'
import InfiniteScroll from 'react-infinite-scroll-component'
import HorizontalScroll from "react-scroll-horizontal";

import Create from '../Context/Create';
const Infi=()=>{



 
    const auth = useContext(Create)
    const [eznum,setenum] = useState(12)
    const [arr,setarr] = useState([])
    async function fetcfi(){
        
        let resp = await fetch(`https://api.nasa.gov/planetary/apod?api_key=gaff4Pwpu0Qg6woyFty1YhVRxhj4In1ImvOCyFD7&start_date=2020-09-01&end_date=2020-10-01&thumbs=true`)
        let data = await resp.json()
        let iarr  = []
        for (let i = 0; i < 7; i++) {
            iarr.push(data[i])
            
        }
        setarr([iarr])
        
    }
    useEffect(()=>{
        fetcfi()
       
    },[])
   async function fetchData(){
    
 
    setenum(eznum-1)
    
   
   
    let resp = await fetch(`https://api.nasa.gov/planetary/apod?api_key=gaff4Pwpu0Qg6woyFty1YhVRxhj4In1ImvOCyFD7&start_date=2020-${eznum-1}-01&end_date=2020-${eznum}-01&thumbs=true`)
    let data = await resp.json()
   
    
   
    let iarr  = []
    for (let i = 0; i < 7; i++) {
        iarr.push(data[i])
        
    }
    setarr([...arr,iarr])
   
    console.log(arr)
   }
   
   function cardclickedz(e){
    console.log(e.target.src)
    auth.setPlayurl(e.target.src)
    auth.setSidev(true)
   }

    return(
        <InfiniteScroll
  dataLength={arr.length} 
  next={fetchData}
  hasMore={true}
  loader={<h4>Loading...</h4>}
  endMessage={
    <p style={{ textAlign: 'center' }}>
      <b>Yay! You have seen it all</b>
    </p>
  }
>
  {
    <div className='infi_wol'>
        {arr.map((items, index) => {
        return (
          <div className='infi_cvr' >
            <HorizontalScroll
      pageLock={true}
      reverseScroll={true} >
             {items.map((sitems, sIndex) => {
              return <div className='in_card' >
                <p style={{opacity:'0'}}>{sitems.title}</p>
                    {sitems.media_type=='image'?<img className='in_card_img' src={sitems.url} onClick={cardclickedz}></img>:
                     <img className='in_card_img'  src={sitems.thumbnail_url} onClick={()=>{console.log( auth.setPlayurl(sitems.url),auth.setSidev(true))}}></img>
                       
                    }
                     
                        <div className='incard_txt'>
                            <span className='incard_title'>{sitems.title}</span>
                            <span className='incard_date'>{sitems.date}</span>
                        </div>

              </div>
            })}
</HorizontalScroll>
         
           
          </div>
        );
      })}
    </div>
   
  } 
</InfiniteScroll>
    )
}
export default Infi